<template>
    <div class="wrap">
      <div class="hint-box">
          <h3>朴新vue 项目基础配置及注意事项</h3>
          <ol class="my-hint">
            <li>
              <p style="color:red; font-weight:bold">整个项目中目录及路由禁止有大写，文件名可以用 - 链接 如：文件名字 'my-test-com'，路由路径：my_test</p>
            </li>
            <li>所有公用组件放到 src/components 下 新那一个该组件的文件夹</li>
            <li>所有页面放到src/view文件夹下</li>
            <li>所有工具类的放到src/utils文件夹里 可根据情况来决定是否需要在里面建立子文件夹（如果新建要遵循第一条） 现有ajax方法 和bus（bus已添加到全局的vue中）ajax的使用可看 /test/ajax</li>
            <li>ajax方法中对时间戳进行了处理 请求时将毫秒转秒 效应后 将秒级转换成毫秒级;  转换规则是根据字段名time:结束  /time":"\d+|time":\d+/</li>
            <li>store里是vuex预留的文件夹</li>
            <li>
              在webpack.base.config.js中定义了几种文件的别名 可快速引用该文件
              'css':resolve('src/assets/css'),
              'img':resolve('src/assets/img'),
              'view':resolve('src/view'),
              'utils':resolve('src/utils'),
              'api':resolve('src/api')
            </li>
            <li>
              路由写法必须要用缓加载
            </li>
            <li>
              一个项目中ui框架不能超过三个 erp后台主要以element 为主
            </li>
          </ol>
      </div>
    </div>
</template>

<script>
    export default {
        name: "index"
    }
</script>

<style lang="less" scoped>
  .wrap{
    display:flex;
    align-items:center;
    justify-content:center;
    height:50vh;

  }
  .my-hint{
    max-width:800px;
    text-align:left;
    margin: 0 auto;
    li{
      font-size: 18px;
      line-height: 1.6;
    }
  }
</style>



